}

body {
  text-align: center;
  background-image: url(/yoxi_game/yoxi_game/static/media/background.f91f4d21.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-color: #464646;
}

.App {
  text-align: center;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.button_radius {border-radius: 50%;}

.button {
  background-color: red; /* Green */
  border: none;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.content-menu {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap-reverse;
	justify-content: space-around;
	align-items: stretch;
	align-content: space-around;
}

.top-20px {
  margin-top: 400px;
}

.footer-copyright {
  margin-top: 30px;
  background-color: deepskyblue;
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%;
  border: 3px solid black;
}

